.hero {
    position: relative;
  }
  .hero h3 {
    font-weight: 100;
    letter-spacing: 2px;
  }
  .hero h1 {
    font-size: 60px;
    font-weight: bold;
  }
  
  .hero h1 span {
    color: #e6225d;
  }
  .hero h2 {
    font-size: 50px;
    font-weight: bold;
    color: #e6225d;
  }
  .hero h2 span {
    color: #0b6aaa;
  }
  .hero p {
    color: #878e99;
    font-size: 18px;
    line-height: 30px;
    margin-top: 30px;
  }
  .hero_btn {
    margin-top: 20%;
  }
  .hero_btn h4 {
    font-weight: 500;
    letter-spacing: 2px;
    color: #43474b;
    font-size: 15px;
    margin: 30px 0 30px 0;
  }
  .hero_btn button {
    margin-right: 30px;
  }
  .hero_btn i {
    font-size: 20px;
  }
  
  .hero_btn button img {
    width: 20px;
    height: 20px;
  }
  .hero .left {
    margin-right: 10%;
  }
  .hero .right {
    width: 40%;
    position: relative;
  }
  .hero .right_img {
    border-radius: 10px;
    height: 100%;
    width: 100%;
    margin-top: 20%;
  }
  .hero .right_img img {
    position: absolute;
    top: 0;
    width:100%;
    border-radius:20%;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  }
  @media (max-width: 768px) {
    .hero .right,
    .hero .left {
      width: 100%;
      /* margin-left: 20px; */
    }
  
    .hero .f_flex {
      flex-direction: column;
      flex-direction: column-reverse;
    }
    .hero .right_img {
      width: 100%;
      height: 57vh; 
    }
    .right{
      width: 100%;
    }
    .right_img{
      width: 100%;
    }
    .right_img img{
      width:100%;
      height:auto;
      /* margin-top: 50%; */
    }
    
  }