  .Portfolio .box {
    border-radius: 10px;
    position: relative;
    width: 600px;
  }
  .Portfolio .box:hover {
    background: none;
    transform: translateY(-0);
  }
  
  .Portfolio .img {
    height: 300px;
    overflow: hidden;
    transition: 0.5s;
  }
  .Portfolio img {
    width: 100%;
    border-radius: 10px;
    transition: 0.5s;
  }
  .Portfolio .box .img:hover img {
    transform: scale(1.2);
  }
  .Portfolio .category {
    margin: 20px 0 30px 0;
    text-transform: uppercase;
  }
  .Portfolio .category span {
    color: #e6225d;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
  }
  .Portfolio .arrow {
    position: absolute;
    right: 30px;
    bottom: 11%;
    transform: rotate(-45deg);
    color: #e6225d;
    opacity: 0;
    transition: 0.5s;
  }
  .Portfolio .title:hover .arrow {
    opacity: 1;
  }
  .Portfolio h2 {
    font-size: 18px;
    transition: 0.5s;
    padding-bottom: 30px;
  }
  .Portfolio .box:hover h2 {
    color: #e6225d;
  }
