  /*------------popup--------*/
  body.active-modal {
    overflow-y: hidden;
  }
  
  .btn-modal {
    padding: 10px 20px;
    display: block;
    margin: 100px auto 0;
    font-size: 18px;
  }
  .ab{
    font-weight: 500;
  }
   
  
  .modal,
  .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 9999;
  }
  
  .overlay {
    background-color: #f8f8ff;
  }
  .modal-content {
    position: absolute;
    top: 12%;
    left: 8%;
    line-height: 1.4;
    background:#f8f8ff;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 85%;
    min-width: 60%;
    z-index: 9999;
    box-shadow: 4px 4px 8px #f8f8ff;
    padding: 50px;
    border-radius: 10px;
  }
  
  .modal-text {
    padding: 0 100px 0 50px;
  }
  .modal-text h1 {
    margin: 20px 0 20px 0;
    font-size: 30px;
  }
  .modal-text p {
    font-size: 17px;
    margin-bottom: 20px;
    line-height: 25px;
  }
  .modal-text button {
    margin-right: 20px;
    color: #e6225d;
  }
  .modal-text button i {
    margin-left: 10px;
  }
  .modal-text button.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    text-align: center;
    border-radius: 50%;
    padding: 0 0;
    font-size: 20px;
  }
  .modal-text button.close-modal i {
    margin: 0;
  }
  /*------------popup--------*/
  @media (max-width: 768px) {
    .modal{
      width:100%;
      height: auto;
    }
    .modal,
    .overlay {
      overflow: scroll;
      margin: 0;
      padding:0;
    }
  
    /* .modal .modal-img {
      margin: 50px 0 50px 0;
    } */
    .modal-content {
      flex-direction: column;
    }
    .modal .right,
    .modal .left {
      padding: 0;
     
    }

    .modal-img img{
      width:100%;

    }
  }
